* {
  box-sizing: border-box;
  font-family: "IBM Plex Sans KR", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-direction: column;
  background-color: #2b283a;
  height: 100%;
}
.light-container {
  display: flex;
  flex-direction: column;
  background: linear-gradient(#227d96, #51d390);
  height: 100%;
}

/* NAV styles */

nav {
  height: 7vh;
  display: flex;
  align-items: center;
  background-color: #f1f1f1;
  padding: 30px 25px;
  box-shadow: 1px 5px 5px #04040440;
  z-index: 1;
}

nav.dark {
  background-color: #2b283a;
}

.nav--logo_icon {
  width: 30px;
  margin-right: 10px;
}

.nav--logo_text {
  color: #227d96;
  margin-right: auto;
}

.nav--title {
  color: #deebf8;
}

.toggler {
  display: flex;
  align-items: center;
}

.toggler p {
  font-size: 0.625rem;
  font-weight: 600;
}

.toggler--slider {
  width: 25px;
  height: 13px;
  background-color: #2b283a;
  border-radius: 2rem;
  margin-inline: 7px;
  cursor: pointer;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #2b283a;
  box-sizing: content-box;
}

nav.dark .toggler--slider {
  border-color: white;
  background-color: #f1f1f1;
}

.toggler--slider--circle {
  height: 13px;
  width: 13px;
  background-color: #f1f1f1;
  border-radius: 50%;
}

nav.dark .toggler--slider--circle {
  background-color: #2b283a;
}

.toggler--light {
  color: #2b283a;
}

nav.dark .toggler--light {
  color: #918e9b;
}

.toggler--dark {
  color: #d5d4d8;
}

nav.dark .toggler-dark {
  color: #ffffff;
}

nav.dark .toggler--slider {
  justify-content: flex-end;
}

main {
  width: 100%;
  height: 83vh;
  padding: 40px 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.box {
  width: 500px;
  height: 500px;
  background-color: #f1f1f1;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 20px;
  align-items: center;
}
.inner-box {
  border-radius: 10px;
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
}
.die {
  box-shadow: 2px 3px 4px 2px #dedede;
  border-radius: 5px;
  cursor: pointer;
}
.die-cell {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 2px;
  /* font-size: 2rem; */
}
.roll {
  width: 200px;
  height: 70px;
  background-color: #3f4cb0;
  color: #f1f1f1;
  border: none;
  box-shadow: 1px 1px 7px 2px #cccbcb;
  border-radius: 5px;
  font-size: 1.5rem;
  cursor: pointer;
}
.roll:focus {
  outline: none;
}

.roll:active {
  box-shadow: inset 5px 5px 10px -3px #000000b3;
}

.dots {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dot {
  width: 10px;
  height: 10px;
  margin: 2px;
  background-color: #2b283a;
  border-radius: 50%;
}
footer {
  width: 100%;
  min-height: 10vh;
  color: #f1f1f1;
  background-color: #22202e;
  display: flex;
  justify-content: center;
  align-items: center;
}
